/*Modern Waterfall**/
.themeModrnWatrFall .flatCard .post{
    box-shadow: none;
    background-clip: padding-box;
    border:0px;
    border-radius: 12px;
    overflow: hidden;
}
.themeModrnWatrFall .authorDetails img{ 
    position:static; float: none; clear:both; 
    margin:0px 0 0 0; width:50px; height:50px;
}
.themeModrnWatrFall .authorDetails .author-avatar-block {
    height: 56px;
    width: 56px;
    border: 3px solid #ffffff;
    color: #ffffff;
}

.themeModrnWatrFall .authorDetails .author-avatar-block span {
    text-align: center;
    width: 100%;
}

.themeModrnWatrFall .flatCard h4 .author-avatar-block {
    float: none;
    clear: both;
    margin: 0px auto 7px auto;
    display: block;
    border-radius: 50%;
    -webkit-border-radius: 50%;
}

.themeModrnWatrFall .authrHandleTime .timePost {
    font: 600 10px/18px "open sans";
    text-decoration: none;
    display: inline-block;
    overflow: hidden;
    margin-right: 0;
}


.themeStart46 .authorDetails .author-avatar-block {
    border: 3px solid #ffffff;
    color: #ffffff;
    height: 48px;
    width: 48px;
    margin-top: -5px;
    margin-right: 8px;
    float: left;
}

.themeStart46 .authorDetails .author-avatar-block span {
    text-align: center;
    width: 100%;
}

.themeStart46 .flatCard h4 .author-avatar-block {
    clear: both;
    margin: 0px auto 7px auto;
    display: block;
    border-radius: 50%;
        -webkit-border-radius: 50%;
}

.themeModrnWatrFall .authorDetails .authrHandle, 
.themeModrnWatrFall .authorDetails .timePost, 
.themeModrnWatrFall .sepratedot{
    float:none;
}
.themeModrnWatrFall .authorDetails .authrHandleTime{ line-height:100%;}
.themeModrnWatrFall .authorDetails .authrHandleTime *{vertical-align: middle; display:inline-block; margin:0; line-height: inherit; float:none; clear:both;}
.themeModrnWatrFall .authorDetails .authrHandleTime .sepratedot { margin: 0 5px !important; padding-top: 2px; }
.themeModrnWatrFall .flatCard h4 .postNetwork {  
    width: auto;
    height: auto;
    margin:8px auto;
    float:none;
}
.themeModrnWatrFall .flatCard h4 .postNetwork i{ font-size:30px !important;}
.themeModrnWatrFall .flatCard.onlyTextCard h4 .authorDetails{
    position: static;
    width: 100%;
}
.themeModrnWatrFall .flatCard.onlyTextCard h4 .postNetwork{
    position:static;
    transform:none;
    -moz-transform:none;
    -webkit-transform:none;
    margin-top: 10px;
}
.themeModrnWatrFall .flatCard.onlyTextCard .authorDetails img{margin-top:10px;}
.themeModrnWatrFall .flatCard.onlyTextCard .authorDetails .postAuthorName{margin-top:10px;}
.themeModrnWatrFall .flatCard.onlyTextCard .postContent .postedTxt{
    position:static;
    transform:none;
    -moz-transform:none;
    -webkit-transform:none;
}

.themeModrnWatrFall .authorDetails .postAuthorName {
    margin: 0 auto;
}


.themeModrnWatrFall.display #marqueecontainer{
    height: 100vh;
    scrollbar-width: none;
}

.video-auto__{
    display: flex;
    align-items: stretch;
    justify-content: stretch;
}
.tb_mwf_post_media_wrapp .video-auto__,
.tb_mwf_post_media_wrapp .video-auto__ video{
    left: 0;
    right: 0;
    top: 0;
    position: absolute;
    background-color: #000;
}
.tb_mwf_post_media_wrapp .video-auto__ video{
    width: 100%;
    height: 100%;
}
.tb_mwf_post_media_wrapp .image img.img-responsive {
    margin: 0 auto;
    min-height: 40px;
}

.tb_mwf_post_media_wrapp .image img.img-responsive {
    width: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
}
.tb_mwf_post_media_wrapp .blur-bg{
    bottom: 0;
    background-size: cover;
    background-position: 50% 50%;
    opacity: 1;
}
.blur-bg:before, .tb_mwf_post_media_wrapp .blur-bg{
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
}
.tb_mwf_post_media_wrapp div.image{
    position: relative;
}

.themeModrnWatrFall.display #wPosts{
    margin-bottom: 400px;
}

.themeStart46 #marqueecontainer{
    height: 100vh !important;
    scrollbar-width: none;
}    

.themeStart46  #wPosts{
    margin-bottom: 400px;
}

#marqueecontainer::-webkit-scrollbar {
    width: 0;
    background-color: #f5f5f5
}

.tb_fc_media_wrap{
    position: relative;
    line-height: 0px;
}
.tb_fc_media_wrap video,
.tb_fc_media_wrap iframe{
    width:100%;
    height:100%;
    object-fit: cover;
}
.tb_mwf_contant_wrapper{
    padding: 10px 0px 0px 0px;
}
.tb_mwf_author_wrapper{
    width: 100%;
}
#focus_63 .tb_mwf_author_wrapper{
    margin-bottom: 0px;
}
.tb_mwf_contant_wrapper .tb_mwf_content{
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
}
.tb_mwf_post_media_wrapp .tb_fc_youtube_ico{
    display: none;
}